* {
  box-sizing: border-box;
  -o-box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
}

html {
  scroll-behavior: smooth;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

button, button:focus {
  cursor: pointer;
  border: none;
  outline: none;
}

textarea {
  resize: none;
  outline: none;
}

textarea:focus {
  border: none;
  outline: none;
}

input {
  border: none;
}

input:focus {
  outline: none;
}

html {
  font-size: 62.5%;
}

body {
  color: #333;
  background-image: linear-gradient(to top left, #753682 0%, #bf2e34 100%);
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Nunito, sans-serif;
  font-weight: 400;
  display: flex;
}

:root {
  --color: red;
  --main-transition: all .5s;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (width >= 1200px) {
  .container {
    width: 1170px;
  }
}

main {
  backdrop-filter: blur(200px);
  filter: blur();
  background-color: #ffffff59;
  border-radius: 9px;
  width: 100rem;
  height: 60rem;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3rem 5rem #00000040;
}

.player {
  flex-direction: column;
  flex: 50%;
  align-items: center;
  padding: 9rem;
  transition: all .75s;
  display: flex;
}

.name {
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 2px;
  margin-bottom: 1rem;
  font-size: 4rem;
  font-weight: 300;
  position: relative;
}

.score {
  color: #c7365f;
  margin-bottom: auto;
  font-size: 8rem;
  font-weight: 300;
}

.player--active {
  background-color: #fff6;
}

.player--active .name {
  font-weight: 700;
}

.player--active .score {
  font-weight: 400;
}

.player--active .current {
  opacity: 1;
}

.current {
  opacity: .8;
  color: #fff;
  text-align: center;
  background-color: #c7365f;
  border-radius: 9px;
  width: 65%;
  padding: 2rem;
  transition: all .75s;
}

.current-label {
  text-transform: uppercase;
  color: #ddd;
  margin-bottom: 1rem;
  font-size: 1.7rem;
}

.current-score {
  font-size: 3.5rem;
}

.btn {
  color: #444;
  text-transform: uppercase;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #fff9;
  border: none;
  border-radius: 50rem;
  padding: .7rem 2.5rem;
  font-family: inherit;
  font-size: 1.8rem;
  font-weight: 400;
  transition: all .2s;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 1.75rem 3.5rem #0000001a;
}

.btn:first-letter {
  margin-right: .7rem;
  font-size: 2.4rem;
  display: inline-block;
}

.btn:active {
  transform: translate(-50%, 3px);
  box-shadow: 0 1rem 2rem #00000026;
}

.btn:focus {
  outline: none;
}

.dice {
  height: 10rem;
  position: absolute;
  top: 16.5rem;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2rem 5rem #0003;
}

.btn--new {
  top: 4rem;
}

.btn--roll {
  top: 39.3rem;
}

.btn--hold {
  top: 46.1rem;
}

.player--winner {
  background-color: #2f2f2f;
}

.player--winner .name {
  color: #c7365f;
  font-weight: 700;
}

.hidden {
  display: none;
}

@media (width >= 768px) {
  .container {
    width: 750px;
  }
}

@media (width >= 992px) {
  .container {
    width: 970px;
  }
}

@media (width >= 1200px) {
  .container {
    width: 1170px;
  }
}
